.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height:       100vh;
  display:          flex;
  flex-direction:   column;
  align-items:      center;
  justify-content:  center;
  font-size:        calc(10px + 2vmin);
  color:            rgb(0, 0, 0);
}

.App-link {
  color: #61dafb;
}
